<template>
    <div style="padding-top: 2px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="项目" prop="project">
                <el-input v-model="searchFormData.project" placeholder="请输入项目"></el-input>
            </el-form-item>
            <el-form-item label="项目名称" prop="project_name">
                <el-input v-model="searchFormData.project_name" placeholder="请输入项目名称"></el-input>
            </el-form-item>
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[14, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="project"
                    label="项目"
                    width="260">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="project_name"
                    label="项目名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="project_context"
                    label="项目上下文"
                    width="180">
            </el-table-column>
            <el-table-column
                    :formatter="format_project_type"
                    prop="project_type_code_id"
                    label="项目类型"
                    width="120">
            </el-table-column>
            <el-table-column
                    :formatter="format_project_env"
                    prop="project_env_code_id"
                    label="项目环境"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="project_version"
                    label="项目版本"
                    width="120">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="project_team_id"
                    :formatter="format_project_team"
                    label="项目团队"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="180">
            </el-table-column>
            <el-table-column
                    fixed="right"
                    :formatter="format_project_state"
                    prop="project_state_code_id"
                    label="项目状态"
                    width="180">
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="180">
                <template slot-scope="scope">
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="生成权限" placement="left">
                            <el-button @click.native.prevent="onRecreateAuthority(scope.$index, tableData)" type="text" size="mini"><i class="iconfont icon-quanxian"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="查看" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="mini"><i class="iconfont icon-chakan"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="修改" placement="left">
                            <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="mini"><i class="iconfont icon-xiugai"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="删除" placement="left">
                            <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="mini"><i class="iconfont icon-shanchu icon-red"></i></el-button>
                        </el-tooltip>
                    </el-col>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'projectInfoManageList',
        data() {
            return {
                module_name: 'projectInfoManage',
                refresh_page_method: 'refresh_' + this.module_name,
                project_type_code_list: null,
                project_env_code_list: null,
                project_state_code_list: null,
                project_team_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1,
                    order: '+project'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.project_type_code_list = await this.$setCodeList(this.project_type_code_list, 'project_type');
            this.project_env_code_list = await this.$setCodeList(this.project_env_code_list, 'project_env');
            this.project_state_code_list = await this.$setCodeList(this.project_state_code_list, 'project_state');
            let project_team_code_list_res = await this.$exec({}, 'OAMP_search_project_team_code_list');
            if (project_team_code_list_res != null && project_team_code_list_res.data != null)
                this.project_team_code_list = project_team_code_list_res.data;
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            format_project_type(row, column, cellValue) /*格式化项目类型信息*/ {
                return this.$formatCodeValue(row, this.project_type_code_list, column.property)
            },
            format_project_env(row, column, cellValue)/*格式化项目环信息*/ {
                return this.$formatCodeValue(row, this.project_env_code_list, column.property)
            },
            format_project_state(row, column, cellValue)/*格式化项目状态信息*/ {
                return this.$formatCodeValue(row, this.project_state_code_list, column.property)
            },
            format_project_team(row, column, cellValue)/*格式化项目团队信息*/ {
                return this.$formatCodeValue(row, this.project_team_code_list, column.property, '_id', 'project_team_name')
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let searchData = this.searchFormData;
                let data = await this.$search(searchData, 'OAMP_search_project_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            async onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.$del(rowData, 'OAMP_delete_project_info')) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.search();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            },
            onRecreateAuthority(index, tableData)/*重新生成权限*/ {
                alert('重新生成权限')
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



