<template>
    <el-dialog title="项目信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')"
    >
        <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">
            <el-form-item label="项目" prop="project">
                <el-input v-model="pageData.project"></el-input>
            </el-form-item>
            <el-form-item label="项目名称" prop="project_name">
                <el-input v-model="pageData.project_name"></el-input>
            </el-form-item>
            <el-form-item label="项目上下文" prop="project_context">
                <el-input v-model="pageData.project_context"></el-input>
            </el-form-item>
            <el-form-item label="项目版本" prop="project_version">
                <el-input v-model="pageData.project_version"></el-input>
            </el-form-item>

            <el-form-item label="项目类型" prop="project_type_code_id">
                <el-select v-model="pageData.project_type_code_id" placeholder="请选择项目类型" clearable>
                    <el-option v-for="item in project_type_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="项目环境" prop="project_env_code_id">
                <el-select v-model="pageData.project_env_code_id" placeholder="请选择项目环境" clearable>
                    <el-option v-for="item in project_env_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="所属项目组" prop="project_team_id">
                <el-select v-model="pageData.project_team_id" placeholder="请选择项目类型" clearable>
                    <el-option v-for="item in project_team_code_list"
                               :key="item._id"
                               :label="item.project_team_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="项目状态" prop="project_state_code_id">
                <el-select v-model="pageData.project_state_code_id" placeholder="请选择项目状态" clearable>
                    <el-option v-for="item in project_state_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="默认项目根路径" prop="project_default_root_path">
                <el-input v-model="pageData.project_default_root_path" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="检查服务" prop="project_check_service">
                <el-input v-model="pageData.project_check_service" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="检查结果" prop="project_check_result">
                <el-input type="textarea" v-model="pageData.project_check_result" style="width:696px"></el-input>
            </el-form-item>

            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:696px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel === 'add'" type="primary" @click="onSubmit">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel === 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'projectInfoManageInfo',
        data() {
            return {
                module_name: 'projectInfoManage',
                refresh_page_method: 'refresh_' + this.module_name,
                project_type_code_list: null,
                project_env_code_list: null,
                project_state_code_list: null,
                project_team_code_list: null,
                pageData: {},
                pageDataRules: {
                    project: [{required: true, message: '请输入项目', trigger: 'blur'}],
                    project_name: [{required: true, message: '请输入项目名称', trigger: 'blur'}],
                    project_type_code_id: [{required: true, message: '请输入项目类型', trigger: 'blur'}],
                    project_env_code_id: [{required: true, message: '请输入项目环境', trigger: 'blur'}]
                }
            }
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name)
            }
        },
        async mounted() {
            this.project_type_code_list = await this.$setCodeList(this.project_type_code_list, 'project_type');
            this.project_env_code_list = await this.$setCodeList(this.project_env_code_list, 'project_env');
            this.project_state_code_list = await this.$setCodeList(this.project_state_code_list, 'project_state');
            let project_team_code_list_res = await this.$exec({}, 'OAMP_search_project_team_code_list');
            if (project_team_code_list_res != null && project_team_code_list_res.data != null)
                this.project_team_code_list = project_team_code_list_res.data;
        },
        methods: {
            async onSubmit() // 提交按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$insert(item,'OAMP_insert_project_info');
                        if (res != null) {
                            _this.$message.success('新增信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('新增信息失败');
                        }
                    }
                });
            },
            async onUpdate() // 更新按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$update(item,'OAMP_update_project_info');
                        if (res != null) {
                            _this.$message.success('修改信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('修改信息失败');
                        }
                    }
                });
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


